
//Hoja de estilo para la página de error 404.
.logotipo--logo{
    width: 210px;
    height: auto;
    margin: 10px;
    animation-name: aparecer;
    animation-duration: 2s;
    animation-timing-function: ease-in-out;
}

@keyframes aparecer {
    0%{
        opacity: 0;
        transform: translateX(-100px);
    }
    50%{
        opacity: 0.5;
        transform: translateX(50px);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}


.error--tom{
    width: 250px;
    height: auto;
    display: block;
    margin: auto;
}

.dialogo{
    width: 90vw;
    height: max-content;
    position: absolute;
    top: 640px;
}
.dialogo--h5{
    width: 60%;
    height: 40px;
    text-align: center;
    display: block;
    margin: auto;
    margin-left: 20%;
    padding: 60px;
    padding-bottom: 120px;
    background-color: var(--color-primario);
    border-radius: 70px;
}

#dialogo--error{
    width: max-content;
    text-align: center;
    color: var(--color-letra-input);
    position: absolute;
    left: 46%;
    top: 180px;
}

    
    #dialogo--respuesta{
        width: max-content;
        text-align: center;
        text-decoration: underline;
        position: absolute;
        left: 70%;
        bottom: 160px;
        padding: 20px;
        background-color: rgb(238, 237, 174);
        border-radius: 50px;
        cursor: pointer;
    }
    

    .error__puntero{
        width: 30px;
        height: auto;
        display: block;
        position: absolute;
        left: 62%;
        top: 600px;
        -webkit-transform: rotate(90deg);
        animation: slide 1s linear infinite;

        @keyframes slide {
            from {
                left: 61.5%;
            }
            to{
                left: 62%;
            }
        }
    }


#nombrePersonaje{
    width: 200px;
    text-align: center;
    display: block;
    position: absolute;
    left: 23%;
    top: 600px;
    padding: 10px;
    background-color: rgb(184, 158, 112);
    border-radius: 50px;
    -webkit-transform: rotate(-5deg);
}


//Cuando la pantalla tenga un width menor o igual a 500px se visualizará con los siguientes estilos.
@media screen and (max-width: 500px){

.dialogo{
    width: 500px;
    height: max-content;
    position: absolute;
    top: 640px;
}

.dialogo--h5{
    width: 50%;
    padding: 65px;
    padding-bottom: 120px;
    margin-left: 15%;
}

.dialogo--error{
    top: 160px;
}

#dialogo--respuesta{
    padding: 10px;
    bottom: 170px;
    left: 50%;
}

.error__puntero{
    left: 50%;
    top: 610px;

    @keyframes slide {
        from {
            left: 49.5%;
        }
        to{
            left: 50%;
        }
    }
}

#nombrePersonaje{
    width: max-content;
    left: 15%;
    top: 630px;
    -webkit-transform: rotate(-7deg);
}
}